<template>
   <div style="height:100%">
        <el-menu class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse">
            <div class="collapse_style">
                <transition name="toggle" >
                    <i v-model="isCollapse" @click="handleToggle($event)" :class="icon"></i>
                </transition>
            </div>
            <div class="treeStyle">
                <sideMenu></sideMenu>
            </div>
        </el-menu>
   </div>
</template>
<script>
    import sideMenu from './component/sideMenu'
    export default {
        data: () => ({
            isCollapse: false,
            icon:'fa fa-server'
        }),
        components:{
            sideMenu
        },
        methods: {
            enter:function(){
                console.log('in')
            },
            leave(){
                console.log('in')
            },
            handleToggle($event){
                this.isCollapse = !this.isCollapse
                this.icon = this.isCollapse?'fa fa-tasks':'fa fa-server'
            },
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            }
        }
    }
</script>
<style>
    .collapse_style{
        font-size: 26px;
        color:#909399;;
        position: absolute;
        right: 2px;
        top:0;
        z-index: 1000;
        cursor: pointer;
    }
    .el-menu-vertical-demo{
        height: 100%;
        border-left: 1px solid rgba(236,240,245,1);
        /* background: rgba(236,240,245,1); */
    }
    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 200px;
        min-height: 400px;
    }
    .treeStyle{
        padding: 10px
    }
</style>


