<template>
    <el-tree
        :props="props1"
        :load="loadNode1"
        lazy
        :highlight-current="true"
        show-checkbox
    >
    </el-tree>
</template>
<script>
    export default {
        data() {
            return {
                props1: {
                    label: 'name',
                    children: 'zones',
                    isLeaf: 'leaf'
                },
            };
        },
        methods: {
            loadNode1(node, resolve) {
                if (node.level === 0) {
                    return resolve([{ name: '测试0' }]);
                }
                if (node.level > 1) return resolve([]);

                setTimeout(() => {
                    const data = [{
                        name: '测试',
                        leaf: true
                    }, {
                        name: '测试1'
                    }];
                    resolve(data);
                }, 500);
            }
        }
    };
</script>
<style>
   

</style>



